	module.exports = "<h1 id=vue2-calendar>vue2-calendar</h1> <blockquote> <p>vue 2 calendar, datepicker component which supported lunar or date event</p> </blockquote> <h2 id=build-setup>Build Setup</h2> <pre><code class=lang-bash># install dependencies\nnpm install\n\n# serve with hot reload at localhost:8080\nnpm run dev\n\n# build for production with minification\nnpm run build\n\n# run unit tests\nnpm run unit\n\n# run all tests\nnpm test\n</code></pre> <h2 id=screenshot>Screenshot</h2> <p><img src=" + __webpack_require__(86) + " alt=\"\"></p> <h2 id=usage>Usage</h2> <p>Component path:</p> <p><strong>src/components/Calendar.vue</strong></p> <pre><code class=lang-html>  &lt;calendar\n    :value=&quot;value&quot;\n    :disabled-days-of-week=&quot;disabled&quot;\n    :format=&quot;format&quot;\n    :clear-button=&quot;clear&quot;\n    :placeholder=&quot;placeholder&quot;\n    :pane=&quot;2&quot;\n    :has-input=&quot;false&quot;\n    :on-day-click=&quot;onDayClick2&quot;\n    :special-days=&quot;_dateMap&quot;\n  &gt;&lt;/calendar&gt;\n</code></pre> <h3 id=use-slot-to-render-async-data>Use slot to render async data</h3> <pre><code class=lang-html>\n  &lt;calendar class=&quot;event-calendar&quot; :value=&quot;value&quot; :disabled-days-of-week=&quot;disabled&quot; :format=&quot;format&quot; :clear-button=&quot;clear&quot; :placeholder=&quot;placeholder&quot; :pane=&quot;2&quot; :has-input=&quot;false&quot; :on-day-click=&quot;onDayClick3&quot; :change-pane=&quot;changePane&quot;&gt;\n    &lt;div v-for=&quot;evt in events&quot; :slot=&quot;evt.date&quot;&gt;\n        ${{evt.content}} &lt;i :class=&quot;{low : evt.low}&quot; v-if=&quot;evt.low&quot;&gt;&lt;/i&gt;\n    &lt;/div&gt;\n  &lt;/calendar&gt;\n</code></pre> <h3 id=range-hover-status>Range Hover Status</h3> <pre><code class=lang-html>\n  &lt;calendar :value=&quot;value&quot; :disabled-days-of-week=&quot;disabled&quot; :format=&quot;format&quot;\n   :clear-button=&quot;clear&quot; :placeholder=&quot;placeholder&quot; :pane=&quot;2&quot;\n   :range-bus=&quot;getBus&quot; :range-status=&quot;1&quot;&gt;&lt;/calendar&gt;\n\n  &lt;calendar :value=&quot;value&quot; :disabled-days-of-week=&quot;disabled&quot; :format=&quot;format&quot;\n   :clear-button=&quot;clear&quot; :placeholder=&quot;placeholder&quot; :pane=&quot;2&quot;\n   :range-bus=&quot;getBus&quot; :range-status=&quot;2&quot;&gt;&lt;/calendar&gt;\n</code></pre> <h3 id=props>Props</h3> <p>Options/Props</p> <table> <thead> <tr> <th>Name</th> <th>Type</th> <th>Default</th> <th>Description</th> </tr> </thead> <tbody> <tr> <td>value</td> <td><code>String</code></td> <td>&#39;&#39;</td> <td>Value of the input DOM</td> </tr> <tr> <td>width</td> <td><code>String</code></td> <td>&#39;200px&#39;</td> <td>Width of the input DOM</td> </tr> <tr> <td>format</td> <td><code>String</code></td> <td><code>MMMM/dd/yyyy</code></td> <td>The date format, combination of d, dd, M, MM, MMM, MMMM, yyyy.</td> </tr> <tr> <td>disabled-days-of-week</td> <td><code>Array</code></td> <td></td> <td>Days of the week that should be disabled. Values are 0 (Sunday) to 6 (Saturday). Multiple values should be comma-separated.</td> </tr> <tr> <td>clear-button</td> <td><code>Bollean</code></td> <td>false</td> <td>If true shows an  shaped button to clear the selected date. Usefull in forms where date entry is optional.</td> </tr> <tr> <td>placeholder</td> <td><code>String</code></td> <td></td> <td>Placeholder to put on the input field when no date (null or empty) is set</td> </tr> <tr> <td>hasInput</td> <td><code>Boolean</code></td> <td>true</td> <td>Default is has-input style, if don&#39;t have input will show pane directly</td> </tr> <tr> <td>pane</td> <td><code>Number</code></td> <td>1</td> <td>pane count</td> </tr> <tr> <td>borderWidth</td> <td><code>Number</code></td> <td>2</td> <td>This value is for calculating the pane width</td> </tr> <tr> <td>onDayClick</td> <td><code>Function</code></td> <td></td> <td>Only for <code>hasInput</code> set false</td> </tr> <tr> <td>specialDays</td> <td><code>Object</code></td> <td></td> <td>To repalce the day text</td> </tr> <tr> <td>changePane</td> <td><code>Function</code></td> <td></td> <td>For pane change parameter (year, month, pane) month[0,11], demo <code>/src/modules/Docs.vue</code></td> </tr> <tr> <td>rangeBus</td> <td><code>Function</code></td> <td></td> <td>should return <code>new Vue()</code> as sibling component communication events bus</td> </tr> <tr> <td>rangeStatus</td> <td><code>Number</code></td> <td>0</td> <td>Default is <code>disabled</code> range hover effect, currently only support [0,1,2] <code>1</code> will communicate with <code>2</code></td> </tr> </tbody> </table> <pre><code class=lang-javascript>  props: {\n    value: {\n      type: String\n    },\n    format: {\n      default: &#39;MM/dd/yyyy&#39;\n    },\n    disabledDaysOfWeek: {\n      type: Array,\n      default () {\n        return []\n      }\n    },\n    width: {\n      type: String,\n      default: &#39;200px&#39;\n    },\n    clearButton: {\n      type: Boolean,\n      default: false\n    },\n    lang: {\n      type: String,\n      default: navigator.language\n    },\n    placeholder: {\n      type: String\n    },\n    hasInput: {\n      type: Boolean,\n      default: true\n    },\n    pane: {\n      type: Number,\n      default: 1\n    },\n    borderWidth: {\n      type: Number,\n      default: 2\n    },\n    onDayClick: {\n      type: Function,\n      default: function () {}\n    },\n    changePane: {\n      type: Function,\n      default: function () {}\n    },\n    specialDays: {\n      type: Object,\n      default () {\n        return {}\n      }\n    },\n    rangeBus: {\n      type: Function,\n      default: function () {\n        // return new Vue()\n      }\n    },\n    rangeStatus: {\n      type: Number,\n      default: 0\n    }\n  }\n</code></pre> <h2 id=credits>Credits</h2> <p>Inspired by <a href=https://github.com/yuche/vue-strap>vue-strap</a> datepicker component.</p> <p>For detailed explanation on how things work, checkout the <a href=http://vuejs-templates.github.io/webpack/ >guide</a> and <a href=http://vuejs.github.io/vue-loader>docs for vue-loader</a>.</p> ";

